import { useEffect } from 'react';

//Component imports
import Background from '../components/Background/Background';

//Library imports
import AOS from 'aos';

//Style imports
import 'aos/src/sass/aos.scss';
import './styles/index.scss';

export default () => {
  useEffect(() => {
    AOS.init({offset: 10});
  },[]);

  const current = 0;

  return (
    <div id='main-wrapper'>
      <Background active={current !== 0} />
    </div>
  )
}
  
